<template>
  <div class="login">
    <span class="logo"></span>

    <cube-input class="mb20" type="text" placeholder="请输入用户名" v-model="userName" :clearable="true" :maxlength="5"></cube-input>
    <cube-input class="mb50" type="password" placeholder="请输入密码" v-model="password" :eye="{open: false, reverse: false}" :clearable="true" :maxlength="20"></cube-input>

    <cube-button @click="login" :primary="true">登录</cube-button>
  </div>
</template>

<script>
export default {
  data () {
    return {
      userName: null,
      password: null
    }
  },

  methods: {
    login () {
      if (this.userName && this.password) {
        this.$router.push({ path: '/selfLiftingOrder' })
      } else {
        this.$createToast({
          type: 'error',
          txt: '请输入任意的用户名和密码',
          time: 1500
        }).show()
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
.login {
  padding: 30px;

  .logo {
    display: block;
    width: 150px;
    height: 150px;
    margin: 0 auto 20px;
    background: url('../assets/img/logo.png') no-repeat center;
    background-size: auto 75%;
  }
}
</style>



